export const Appheader = {
  SetHeaders: (token) => {
    if (token != undefined) {
      return {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + token,
      };
    } else {
      return {
        "Content-Type": "application/json",
        Accept: "application/json",
      };
    }
  },
};
