import Axios from "axios";
import { Apis } from "./apis";
import { Appheader } from "./apis";
import Auth from "../Auth/auth";
import swal from "sweetalert";

export const Service = {
  login: async (
    state,
    props,
    setInvalidInputs,
    setLoginLoading,
    setPending
  ) => {
    console.log("URL ----", Apis.login);
    await Axios.post(Apis.login, {
      ...state,
      device_token: localStorage.getItem("@fcmToken"),
    })
      .then(async (res) => {
        if (res.data.data) {
          console.log(res.data);
          localStorage.setItem("@appToken", res.data.data.access_token);
          setPending(false);
          setInvalidInputs(false);
          setLoginLoading(false);
          const Headers = Appheader.SetHeaders(res.data.data.access_token);
          await Axios.get(Apis.me, { headers: Headers })
            .then(async (res) => {
              Auth.authenticate();
              console.log("me: ", res.data.data);
              console.log(res.data.data);
              localStorage.setItem("@user", res.data.data);
              props.history.push("dashboard");
            })
            .catch((err) => {
              console.log("inside me", err.response.data.message);
              setInvalidInputs(true);
              setLoginLoading(false);
            });
          console.log("here");
          return true;
        } else {
          console.log("Data nahi mila", res.data.message);
          setPending(true);
          setLoginLoading(false);
        }
      })
      .catch((err) => {
        console.log(err);
        console.log(err.response.data.message);
        setPending(false);
        setInvalidInputs(true);
        setLoginLoading(false);
        return false;
      });
  },
  users: (setState, setCsv, setLoading,name) => {
    setLoading(true);
    Axios.get(
      // Apis.users + `?start=0&rows=10&name=${name}`,
      name?Apis.users + `?name=${name}`:Apis.users,
      // Apis.users,
      {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + localStorage.getItem("@appToken"),
        },
      }
    )
      .then((res) => {
        setLoading(false);
        if (res.data.status == 1) {
          setCsv(res.data.data);
          setState(res.data.data);
          console.log('DAta',res.data.data)
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.message);
      });
  },
  states: (setState, setLoading) => {
    setLoading(true);
    Axios.get(Apis.states, {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("@appToken"),
      },
    })
      .then((res) => {
        setLoading(false);
        if (res.data.status == true) {
          setState(res.data.data);
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.message);
      });
  },
  deletestate: (id, setState) => {
    // setLoaderLoading(true);
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    Axios.delete(Apis.deletestate + `?store_id=${id}`, { headers: Headers })
      .then((res) => {
        // setLoaderLoading(false);
        if (res.data.status == true) {
          setState([]);
        }
      })
      .catch((err) => {
        console.log(err.message);
      });
  },

  managestate: (id, setState) => {
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    console.log("Headers", Headers);
    Axios.post(Apis.managestate + `?state_id=${id}`, {}, { headers: Headers })
      .then((res) => {
        if (res.data.status == true) {
          setState([]);
        }
      })
      .catch((err) => {
        swal("Oops!", "Something went wrong!", "error");
      });
  },
  managereturn: (id,status, setReturns,getReturns) => {
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    console.log("Headers", Headers);
    Axios.post(Apis.managereturn + `?return_id=${id}&return_status=${status}`, {}, { headers: Headers })
      .then((res) => {
        if (res.data.status == true) {
          setReturns([]);
          getReturns();
        }
      })
      .catch((err) => {
        swal("Oops!", "Something went wrong!", "error");
      });
  },
  returns: (setState, setLoading) => {
    setLoading(true);
    Axios.get(Apis.returns, {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("@appToken"),
      },
    })
      .then((res) => {
        setLoading(false);
        if (res.data.status == 1) {
          setState(res.data.data);
          console.log(res.data.data);
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.message);
      });
  },
  stores: (setState, setLoading) => {
    setLoading(true);
    Axios.get(Apis.stores, {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("@appToken"),
      },
    })
      .then((res) => {
        setLoading(false);
        if (res.data.status == 1) {
          setState(res.data.data);
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.message);
      });
  },

  managestore: (id, setState, setStores) => {
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    console.log("Headers", Headers);
    Axios.post(Apis.managestore + `?store_id=${id}`, {}, { headers: Headers })
      .then((res) => {
        if (res.data.status == true) {
          setStores([]);
          setState();
          //swal("Success", "Updated Successfully!", "success");
        }
      })
      .catch((err) => {
        swal("Oops!", "Something went wrong!", "error");
      });
  },
  addstores: (state, setLoading) => {
    setLoading(true);
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    Axios.post(Apis.addstore, state, { headers: Headers })
      .then((res) => {
        setLoading(false);
        if (res.data.status == 1) {
          alert(res.data.message);
        } else {
          alert(res.data.message);
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.message);
      });
  },
  deletestores: (id, setState, setStores) => {
    // setLoaderLoading(true);
    const Headers = Appheader.SetHeaders(localStorage.getItem("@appToken"));
    Axios.delete(Apis.deletestore + `?store_id=${id}`, { headers: Headers })
      .then((res) => {
        // setLoaderLoading(false);
        if (res.data.status == true) {
          setStores([]);
          setState();
        }
      })
      .catch((err) => {
        console.log(err.message);
      });
  },
  updatestores: (state, setStores, setLoader) => {
    Axios.post(Apis.updatestore, state, {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("@appToken"),
      },
    })
      .then((res) => {
        setLoader(false);
        if (res.data.status == 1) {
          setStores();
        }
      })
      .catch((err) => {
        console.log(err.message);
      });
  },
};
