import React, { useEffect, useState } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Switch from "@material-ui/core/Switch";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Service } from "../../../config/service";
import CircularProgress from "@material-ui/core/CircularProgress";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Heading from "../heading";
import TablePagination from "@material-ui/core/TablePagination";

const StyledTableCell = withStyles((theme) => ({
  head: {
    // backgroundColor: "rgb(255,255,0,0.9)",
    backgroundColor: "#F2D184",
    color: "black",
    fontFamily: "Poppins",
    fontSize: 18,
    fontWeight: 700,
  },
  body: {
    fontSize: 14,
    fontFamily: "Poppins",
    width: "20%",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 700,
    fontFamily: "Poppins",
    marginTop: 50,
  },
  logo: {
    width: "100%",
    height: "100%",
    objectFit: "contain",
    //backgroundColor:'blue',
    //borderRadius:'20%',
    scale: 2,
  },
  topDiv: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "row",
    alignItems: "center",
  },
  paper: {
    width: "50%",
    height: "50%",
    justifyContent: "center",
    padding: theme.spacing(2),
    alignItems: "center",
  },
  ".MuiTableContainer-root": {
    marginTop: 50,
  },
  loginLoader: {
    marginTop: "5%",
    color: "#da983b",
  },
  center: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
}));

export default function StateTable(props) {
  const classes = useStyles();

  const [state, setState] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);


  useEffect(() => {
    getState();
  }, []);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const getState = async () => {
    await Service.states(setState, setLoading);
  };

  const handleDeleteState = async (id) => {
    await Service.deletestate(id, setState);
  };

  return (
    <>
      <div className={classes.topDiv}>
        <Heading title={"States"} />
      </div>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell align="left">States</StyledTableCell>
            <StyledTableCell align="left">Status</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {state
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((state, index) => (
              <StateTableRow state={state} setState={setState} getState={getState}/>
            ))}
        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={state.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />

      {loading && (
        <div className={classes.center}>
          <CircularProgress size={50} className={classes.loginLoader} />
        </div>
      )}
    </>
  );
}

function StateTableRow({ state, getState,setState }) {
  const [switchO, setSwithO] = useState(state.show_state);
  const handleVisibility = async (id) => {
    setSwithO(!switchO);
    await Service.managestate(id, getState, setState);
  };
  return (
    <StyledTableRow key={state}>
      <StyledTableCell component="th" scope="row">
        {state.state_name}
      </StyledTableCell>
      <StyledTableCell align="left">
        <FormControlLabel
          control={
            <Switch
              color="primary"
              checked={switchO}
              onChange={() => handleVisibility(state._id)}
            />
          }
        />
      </StyledTableCell>
    </StyledTableRow>
  );
}