import React, { useEffect, useState } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Switch from "@material-ui/core/Switch";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import CircularProgress from "@material-ui/core/CircularProgress";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Service } from "../../../config/service";
import Paper from "@material-ui/core/Paper";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import DeleteButton from "../../common/buttons/delete-store";
import AddStoreBtn from "../buttons/add-store";
import Heading from "../heading";
import UpdateButton from "../buttons/update-store";
import TablePagination from "@material-ui/core/TablePagination";

const StyledTableCell = withStyles((theme) => ({
  head: {
    // backgroundColor: "rgb(255,255,0,0.9)",
    backgroundColor: "#F2D184",
    color: "black",
    fontFamily: "Poppins",
    fontSize: 18,
    fontWeight: 700,
  },
  body: {
    fontSize: 14,
    fontFamily: "Poppins",
    width: "20%",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 700,
    fontFamily: "Poppins",
    marginTop: 50,
  },
  logo: {
    width: "60px",
    height: "60px",
    objectFit: "contain",
    //backgroundColor:'blue',
    //borderRadius:'20%',
    scale: 2,
  },
  loginLoader: {
    marginTop: "5%",
    color: "#da983b",
  },
  center: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  topDiv: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "row",
    alignItems: "center",
  },
  paper: {
    width: "50%",
    height: "50%",
    justifyContent: "center",
    padding: theme.spacing(2),
    alignItems: "center",
  },
  ".MuiTableContainer-root": {
    marginTop: 50,
  },
}));

export default function StoreTable(props) {
  const classes = useStyles();

  const [stores, setStores] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  useEffect(() => {
    getStores();
  }, []);

  const getStores = async () => {
    await Service.stores(setStores, setLoading);
  };
  const handleDeleteStores = async (id) => {
    await Service.deletestores(id, getStores, setStores);
  };

  return (
    <>
      <div className={classes.topDiv}>
        <Heading title={"Stores"} />
        <AddStoreBtn />
      </div>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Logo</StyledTableCell>
            <StyledTableCell>Name</StyledTableCell>
            <StyledTableCell align="right">Visiblity</StyledTableCell>
            <StyledTableCell align="right">Update</StyledTableCell>
            <StyledTableCell align="right">Delete</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {stores
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((stores, index) => (
              <StyledTableRow key={stores.name}>
                <StyledTableCell component="section">
                  <Paper
                    elevation={3}
                    component="section"
                    className={classes.paper}
                  >
                    <img src={stores.logo} className={classes.logo} />
                  </Paper>
                </StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {stores.store_name}
                </StyledTableCell>
                <StoreSwitchTableRow store={stores} getStores={getStores} setStores={setStores}/>
                <StyledTableCell align="right">
                  <UpdateButton detail={stores} />
                </StyledTableCell>
                <StyledTableCell align="right">
                  <DeleteButton
                    onClick={() => handleDeleteStores(stores._id)}
                  />
                </StyledTableCell>
              </StyledTableRow>
            ))}
        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={stores.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
      {loading && (
        <div className={classes.center}>
          <CircularProgress size={50} className={classes.loginLoader} />
        </div>
      )}
    </>
  );
}
function StoreSwitchTableRow({ store, getStores,setStores }) {
  const [switchO, setSwithO] = useState(store.show_store);
  const handleVisibility = async (id) => {
    setSwithO(!switchO);
    await Service.managestore(id, getStores, setStores);
  };
  return (
      <StyledTableCell align="right">
        <FormControlLabel
          control={
            <Switch
              color="primary"
              checked={switchO}
              onChange={() => handleVisibility(store._id)}
            />
          }
        />
      </StyledTableCell>
  );
}