import React, { useEffect } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import RefreshIcon from "@material-ui/icons/Refresh";
import TextField from "@material-ui/core/TextField";
import { Service } from "../../../config/service";
import CircularProgress from "@material-ui/core/CircularProgress";
import { colors } from "../../../res/colors";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid black",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(2),
      width: 200,
      fontSize: "20px",
    },
    "& .MuiFormLabel-root": {
      fontSize: "14px",
      color: "#0000008a",
      fontWeight: "700",
      fontFamily: "Poppins",
    },
  },
  loginLoader: {
    position: "absolute",
    bottom: 8,
    right: "42%",
    color: colors.white,
  },
  submitButton: {
    fontSize: "14px",
    backgroundColor: "#8e99ab",
    fontWeight: "700",
    fontFamily: "Poppins",
    padding: "5px",
    alignSelf: "center",
    borderRadius: "20px",
    borderColor: "transparent",
    width: "100%",
    "&:focus": { outline: "none" },
  },
  center: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#F2D184",
    color: "black",
    fontFamily: "Poppins",
    fontWeight: 800,
    fontSize: 20,
    "&:hover": {
      backgroundColor: "black",
      color: "#F2D184",
    },
  },
  updateicon: {
    fontSize: "40px",
    color: "#da983b",
    padding: "2px",
    "&:focus": { outline: "none" },
  },
}));

export default function UpdateButton(props) {
  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [store_name, setStoreName] = React.useState(props.detail.store_name);
  const [condition, setCondition] = React.useState(props.detail.condition);
  const [note, setNote] = React.useState(props.detail.note);
  const [id, setId] = React.useState(props.detail._id);
  const [stores, setStores] = React.useState([]);

  const [loader, setLoader] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const onSuccess = (remarks) => {
    console.log(remarks);
  };

  const handleUpdateStores = async () => {
    let state = {
      store_id: id,
      store_name: store_name,
      note: note,
      condition: condition,
    };
    await Service.updatestores(state, getStores, setStores);
  };

  useEffect(() => {
    getStores();
  }, []);

  const getStores = async () => {
    await Service.stores(setStores, setLoader);
  };

  return (
    <div>
      <Button onClick={handleOpen} style={{ "&:focus": { outline: "none" } }}>
        <RefreshIcon className={classes.updateicon} />
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper} style={{ scrollBehavior: "smooth" }}>
            <div className={classes.root}>
              <div>
                <TextField
                  required
                  id="standard-basic"
                  label="Store Name"
                  value={store_name}
                  onChange={(e) => setStoreName(e.target.value)}
                />
              </div>
              <div>
                <TextField
                  required
                  id="standard-basic"
                  label="Note"
                  value={note}
                  onChange={(e) => setNote(e.target.value)}
                />
              </div>
              <div>
                <TextField
                  required
                  id="standard-basic"
                  label="Condition"
                  value={condition}
                  onChange={(e) => setCondition(e.target.value)}
                />
              </div>

              <div className={classes.center}>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                  disabled={loader}
                  onClick={() => handleUpdateStores()}
                >
                  {loader && (
                    <CircularProgress
                      size={24}
                      className={classes.loginLoader}
                    />
                  )}
                  Update Store
                </Button>
              </div>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
