import React, { useEffect } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import Avatar from "@material-ui/core/Avatar";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Service } from "../../../config/service";
import SearchBar from "../../search-bar/search-bar";
import ExportBtn from "../buttons/export-data";
import { CSVLink } from "react-csv";
import Heading from "../heading";
import CircularProgress from "@material-ui/core/CircularProgress";
import TablePagination from "@material-ui/core/TablePagination";

const headers = [
  { label: "Avatar", key: "avatar" },
  { label: "Name", key: "name" },
  { label: "Mobile No", key: "email" },
  { label: "City", key: "city" },
  { label: "State", key: "state" },
];
const StyledTableCell = withStyles((theme) => ({
  head: {
    // backgroundColor: "rgb(255,255,0,0.9)",
    backgroundColor: "#F2D184",
    color: "black",
    fontFamily: "Poppins",
    fontSize: 18,
    fontWeight: 700,
  },
  body: {
    fontSize: 14,
    fontFamily: "Poppins",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 700,
    fontFamily: "Poppins",
  },
  logo: {
    objectFit: "contain",
    scale: 2,
  },
  paper: {
    width: "20%",
    height: "20%",
    justifyContent: "center",
    padding: theme.spacing(2),
    alignItems: "center",
  },
  topDiv: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "row",
    alignItems: "center",
  },
  loginLoader: {
    marginTop: "5%",
    color: "#da983b",
  },
  center: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
}));

export default function CustomerTable(props) {
  const classes = useStyles();

  const [users, setUsers] = React.useState([]);
  const [selected, setSelected] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  const [loading, setLoading] = React.useState(false);
  const [userName, setUserName] = React.useState("");
  const [csvReport, setcsvReport] = React.useState({
    data: users,
    headers: headers,
    filename: "ReturnBeezUsers.csv",
  });

  useEffect(() => {
    getUsers();
  }, []);
  const setCsv = (value) => {
    setcsvReport({ data: value });
  };

  const searchHandler = async (searchString) => {
    setUsers([]);
    await Service.users(setUsers, setCsv, setLoading, searchString);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const getUsers = async () => {
    await Service.users(setUsers, setCsv, setLoading);
  };
  const downloadReport = (event, done) => {
    // API call to get data
    const objReport = {
      filename: "ReturnBeezUsers.csv",
      headers: headers,
      data: users,
    };
    console.log(objReport);
    setcsvReport({
      data: users,
      headers: headers,
      filename: "ReturnBeezUsers.csv",
    });
    done();
  };
  return (
    <>
      <div className={classes.topDiv}>
        <Heading title={"Customers"} />
        <SearchBar searchHandler={searchHandler} />
      </div>

      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Avatar</StyledTableCell>
            <StyledTableCell>Name</StyledTableCell>
            <StyledTableCell align="right">Email </StyledTableCell>
            <StyledTableCell align="right">Mobile No</StyledTableCell>
            <StyledTableCell align="right">City</StyledTableCell>
            <StyledTableCell align="right">State</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {users
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((users, index) => (
              <StyledTableRow key={users.name}>
                <StyledTableCell component="section">
                  <Avatar src={users.avatar} className={classes.logo} />
                </StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {users.name}
                </StyledTableCell>
                <StyledTableCell align="right">{users.email}</StyledTableCell>
                <StyledTableCell align="right">{users.mobile}</StyledTableCell>
                <StyledTableCell align="right">{users.city}</StyledTableCell>
                <StyledTableCell align="right">{users.state}</StyledTableCell>
              </StyledTableRow>
            ))}
        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={users.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
      {loading && (
        <div className={classes.center}>
          <CircularProgress size={50} className={classes.loginLoader} />
        </div>
      )}
      <CSVLink {...csvReport} asynconclick={true} onclick={downloadReport}>
        <ExportBtn />
      </CSVLink>
    </>
  );
}
