import React, { useEffect } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import CircularProgress from "@material-ui/core/CircularProgress";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Service } from "../../../config/service";
import SeacrhBar from "../../search-bar/search-bar";
import Heading from "../heading";
import TablePagination from "@material-ui/core/TablePagination";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const StyledTableCell = withStyles((theme) => ({
  head: {
    // backgroundColor: "rgb(255,255,0,0.9)",
    backgroundColor: "#F2D184",
    color: "black",
    fontFamily: "Poppins",
    fontSize: 18,
    fontWeight: 700,
  },
  body: {
    fontSize: 14,
    fontFamily: "Poppins",
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    fontFamily: "Poppins",
  },
  topDiv: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "row",
    alignItems: "center",
  },
  loginLoader: {
    marginTop: "5%",
    color: "#da983b",
  },
  center: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  formControl: {
    margin: "10px",
    minWidth: 120,
  },
});

export default function ReturnTable(props) {
  const classes = useStyles();

  const [returns, setReturns] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const [status, setStatus] = React.useState("Pending");
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  useEffect(() => {
    getReturns();
  }, []);

  const getReturns = async () => {
    await Service.returns(setReturns, setLoading);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <>
      <div className={classes.topDiv}>
        <Heading title={"returns"} />
        <SeacrhBar />
      </div>

      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell align="left">Store</StyledTableCell>
            <StyledTableCell align="left">Dimension</StyledTableCell>
            <StyledTableCell align="left">State</StyledTableCell>
            <StyledTableCell align="left">City</StyledTableCell>
            <StyledTableCell align="left">Street</StyledTableCell>
            <StyledTableCell align="left">Zipcode</StyledTableCell>
            <StyledTableCell align="left">Pickup Date </StyledTableCell>
            <StyledTableCell align="left">Status</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {returns.length>0?returns
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((returns, index) => (
              <ReturnItems returns={returns} index={index} setReturns={setReturns}  getReturns={getReturns}/>
            )): 
            <h3 style={{textAlign:'center'}}>No Returns</h3>
            }
        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={returns.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
      {loading && (
        <div className={classes.center}>
          <CircularProgress size={50} className={classes.loginLoader} />
        </div>
      )}
    </>
  );
}

function ReturnItems({ returns, index, setReturns,getReturns }) {
  const [status, setStatus] = React.useState(returns.return_status);
  const classes = useStyles();
  const returnstatuses = [{title:'Return request pending'},{title:'Return request Accepted'},{title:'In Transit'},{title:'Package Picked up'},{title:'Package returned'},{title:'Request Cancelled'}]

  const handleChangeStatus = async (id,event) => {
    setStatus(event.target.value);
    await Service.managereturn(id,event.target.value, setReturns,getReturns);
  };
  useEffect(() => {
    setStatus(returns.return_status);
  }, []);
  return (
    <StyledTableRow key={returns.name}>
      <StyledTableCell align="left">
        {returns.store_id?.store_name}
      </StyledTableCell>
      <StyledTableCell align="left">
        {returns.dimension == "M"
          ? "Medium"
          : returns.dimension == "S"
          ? "Small"
          : "Large"}
      </StyledTableCell>
      <StyledTableCell align="left">
        {returns.pickup_location?.state}
      </StyledTableCell>
      <StyledTableCell align="left">
        {returns.pickup_location?.city}
      </StyledTableCell>
      <StyledTableCell align="left">
        {returns.pickup_location?.street_address}
      </StyledTableCell>
      <StyledTableCell align="left">
        {returns.pickup_location?.zipcode}
      </StyledTableCell>

      <StyledTableCell align="left">{returns.pickup_date}</StyledTableCell>

      <StyledTableCell align="left">
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">Status</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={status}
            onChange={(e)=>handleChangeStatus(returns._id,e)}
          >
            {
              returnstatuses.map(item=>(
              <MenuItem value={item.title}>{item.title}</MenuItem>
              ))
            }
          </Select>
        </FormControl>
      </StyledTableCell>
    </StyledTableRow>
  );
}