export default function AddStoreBtn(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [loading, setLoading] = useState(false);

  const [storeName, setStoreName] = useState("");
  const [condition, setCondition] = useState("");
  const [note, setNote] = useState("");
  const [packages, setPackages] = useState([]);
  const [packagetitle, setPackageTitle] = useState("");
  const [packageprice, setPackagePrice] = useState("");

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const AddPackage = () => {
    if (packagetitle == "" || packageprice == "") {
      alert("All Field Required");
    } else {
      setPackages((state) => [
        ...state,
        { package_title: packagetitle, package_price: packageprice },
      ]);
    }
  };
  const [avatar, setAvatar] = useState("");
  const [file, setFile] = useState(null);
  const [avatarError, setAvatarError] = useState(false);
  const fileUploadRef = useRef(null);

  const [errorText, toggleErrorText] = useState(false);

  const readURL = (input) => {
    var reader = new FileReader();
    reader.readAsDataURL(input.files[0]);
    reader.onload = (e) => {
      setAvatar(e.target.result);
      setAvatarError(false);
      console.log(input.files[0]);
      setFile(input.files[0]);
    };
  };

  const addstorehandler = async () => {
    if (
      avatar == "" ||
      storeName == "" ||
      note == "" ||
      condition == "" ||
      packages.length == 0
    ) {
      return alert("All fields are required");
    }
    console.log(packages);
    let formData = new FormData();
    formData.append("store_name", storeName);
    formData.append("condition", condition);
    formData.append("note", note);
    formData.append("package_list", JSON.stringify(packages));
    formData.append("logo", file);
    Service.addstores(formData, setLoading);
  };

  return (
    <div className={classes.postionButton}>
      <Button
        variant="contained"
        className={classes.deceasedButton}
        type="button"
        onClick={handleOpen}
      >
        Add Store
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper} style={{ scrollBehavior: "smooth" }}>
            {/* Insert form here */}
            <div className={classes.root}>
              <Grid container justify="center" alignItems="center">
                <input
                  type="file"
                  accept="image/*"
                  style={{
                    width: 0,
                  }}
                  ref={fileUploadRef}
                  onChange={(e, n) => {
                    readURL(e.target);
                  }}
                />
                {avatar ? (
                  <Box
                    border={1}
                    borderRadius={50}
                    className={classes.uploadLogo}
                    onClick={() => {
                      fileUploadRef.current.click();
                    }}
                  >
                    <Avatar style={{ width: 100, height: 100 }} src={avatar} />
                  </Box>
                ) : (
                  <Box
                    border={1}
                    borderColor={avatarError ? colors.error : colors.black}
                    borderRadius={50}
                    className={classes.uploadLogo}
                    onClick={() => {
                      fileUploadRef.current.click();
                    }}
                  >
                    <CloudUploadIcon
                      htmlColor={colors.black}
                      fontSize="large"
                    />
                    <Typography variant="h7" className={classes.uploadLogoText}>
                      Upload
                    </Typography>
                    <Typography variant="h7" className={classes.uploadLogoText}>
                      logo
                    </Typography>
                  </Box>
                )}
              </Grid>

              <div>
                <TextField
                  required
                  id="standard-basic"
                  label="Store Name"
                  value={storeName}
                  onChange={(e) => {
                    setStoreName(e.target.value);
                  }}
                />

                <TextField
                  required
                  id="standard-basic"
                  label="Store Note"
                  value={note}
                  onChange={(e) => {
                    setNote(e.target.value);
                  }}
                />
              </div>
              <div>
                <TextField
                  style={{ width: "100%" }}
                  required
                  id="standard-basic"
                  label="Condition"
                  value={condition}
                  onChange={(e) => {
                    setCondition(e.target.value);
                  }}
                />
              </div>

              <div>
                <h4
                  style={{
                    fontFamily: "Poppins",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  Packages
                </h4>
                <TextField
                  required
                  id="standard-basic"
                  label="Package Title"
                  value={packagetitle}
                  onChange={(e) => {
                    setPackageTitle(e.target.value);
                  }}
                />

                <TextField
                  required
                  id="standard-basic"
                  label="Package Rates"
                  value={packageprice}
                  onChange={(e) => {
                    setPackagePrice(e.target.value);
                  }}
                />
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center",
                  aligItems: "center",
                  marginTop: "2%",
                  marginBottom: "2%",
                  padding: 10,
                }}
              >
                <Button
                  style={{}}
                  className={classes.submitButton}
                  onClick={AddPackage}
                >
                  <AddIcon
                    style={{
                      fontSize: "20px",
                      display: "flex",
                      paddingRight: "2px",
                    }}
                  />
                </Button>
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                {packages.map((item, index) => (
                  <div>
                    <TextField
                      required
                      id="standard-basic"
                      disabled={true}
                      label="Package Title"
                      value={item.package_title}
                    />

                    <TextField
                      required
                      id="standard-basic"
                      label="Package Rates"
                      disabled={true}
                      value={item.package_price}
                    />
                  </div>
                ))}
              </div>
              <div className={classes.center}>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                  onClick={addstorehandler}
                  disabled={loading}
                >
                  Submit
                  {loading && (
                    <CircularProgress
                      size={24}
                      className={classes.loginLoader}
                    />
                  )}
                </Button>
              </div>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
